<template>
  <transition name="slide">
    <div v-if="isOpened" :class="{ drawer: true }" class="show">
      <div class="header_drawer">
        <div class="title">
          <h4>
            {{
      $globals.capitalizeFirstLetter(
        gridModel.id == null ? $t('common.add') : $t('common.save'),
      )
    }}
            {{ $t('views.grids.grid') }}
          </h4>
        </div>
        <div class="content-button btn-add">
          <button class="close mr-5" @click="cancel()">
            {{ $globals.capitalizeFirstLetter($t('common.cancel')) }}
            <i class="icon-exit"></i>
          </button>
          <DropDownMenu v-if="(gridModel.id != null && gridModel.draft == 0) ||
      gridModel.id == null
      " class="btn_drp_grille" :items="[
      {
        label: $globals.capitalizeFirstLetter($t('views.grids.draft')),
        onClick: () => selectedOption('0'),
      },
      {
        label: $globals.capitalizeFirstLetter(
          $t('views.grids.publish'),
        ),
        onClick: () => selectedOption('1'),
      },
    ]" icon="icon-down" :text="$globals.capitalizeFirstLetter($t('common.save'))" :btnId="'btn_dropdown_1'"
            :dropdownId="'dropdownMenu_1'" :btnclass="'btn-primary'" />
          <Button v-if="gridModel.id != null && gridModel.draft == 1" @when-cliked="selectedOption('1')"
            :text="$globals.capitalizeFirstLetter($t('common.save'))" />
        </div>
      </div>

      <div class="body_drawer">
        <div class="block-multistep">
          <div class="block" :class="{ active: activeBlock === 0 }">
            <div class="form-group-flex">
              <input-field class="mb-7" name="Nom de la grille d’évolution" :label="$globals.capitalizeFirstLetter($t('components.grid.gridName'))
      " id="1" :defaultValue="gridName" @input="(e) => {
      this.setGridModel({
        ...this.getGridModel,
        ['name']: e.target.value,
      });
    }
      " :hasError="errors != null && errors?.name != null" :errorMessage="errors && errors?.name != null
      ? $t(
        $globals.capitalizeFirstLetter(
          $t('errors.GRID_NAME_REQUIRED'),
        ),
      )
      : ''
      " @blur="detectChange" />
            </div>
            <div class="form">
              <SelectSingle :options="campagnes" :keyLabel="'name'" :keyValue="'id'" :defaultSelected="campagneSelected"
                label="Campagne" :onSelect="(item) => {
      setGridModel({
        ...this.getGridModel,
        ['campagneId']: item.id,
      });

      detectChange();
    }
      " :hasError="errors != null && errors.campagneId != null" :errorMessage="errors && errors.campagneId != null
      ? $t('errors.' + errors.campagneId)
      : ''
      " />
            </div>
            <div class="form-group-flex form-etat pb-4 border-b">
              <div class="form-field">
                <label class="control-label">
                  {{ $globals.capitalizeFirstLetter($t('views.grids.status')) }}
                </label>
              </div>

              <div class="form-field">
                <div class="switch-button">
                  <input class="switch-button-checkbox" type="checkbox" :value="getGridModel.statuts"
                    :checked="getGridModel.statuts === 1" @change="() => {
      this.setGridModel({
        ...this.getGridModel,
        ['statuts']: this.getGridModel.statuts == 1 ? 0 : 1,
      });
    }
      " @blur="detectChange" />
                  <label class="switch-button-label" for="">
                    <span class="switch-button-label-span">Actif</span>
                  </label>
                </div>
              </div>
            </div>
            <h3 class="mt-6 mb-7">
              <strong>
                {{
      $globals.capitalizeFirstLetter(
        $t('components.grid.categories'),
      )
    }}
              </strong>
            </h3>
            <draggable v-model="gridModel.categories" :animation="300">
              <transition-group type="transition" name="flip-list">
                <div class="item-drag" v-for="categorie in gridModel?.categories" :key="categorie.id"
                  @click="editCategory(categorie)">
                  <i class="icon-drag mr-3"></i>
                  {{ categorie.name }}
                </div>
              </transition-group>
            </draggable>

            <div class="add-categr mt-4">
              <button @click="showNextCategory()">
                <span class="mr-2"><i class="icon-plus"></i></span>
                {{
      $globals.capitalizeFirstLetter(
        $t('components.grid.addCategory'),
      )
    }}
              </button>
            </div>
            <span class="mt-2 error" v-if="(errors != null && errors?.categories != null) ||
      errors?.categoryName != null
      ">
              {{
      (errors && errors?.categories != null) ||
        errors?.categoryName != null
        ? $t(
          $globals.capitalizeFirstLetter(
            $t('errors.CATEGORIY_REQUIRED'),
          ),
        )
        : ''
    }}
            </span>
            <span class="mt-2 error" v-if="(errors != null &&
        errors?.nameItem &&
        errors?.nameItem[0] != null) ||
      itemNameErrorMessage
      ">
              {{
      (errors && errors?.nameItem && errors?.nameItem[0] != null) ||
        itemNameErrorMessage
        ? $t(
          $globals.capitalizeFirstLetter(
            $t('errors.ITEM_REQUIRED'),
          ),
        )
        : ''
    }}
            </span>
          </div>
          <div class="block" :class="{ active: activeBlock === 1 }">
            <div class="header_bloc">
              <button @click="showPreviousCategory">
                <i class="icon-previous mr-2"></i>
                {{ $globals.capitalizeFirstLetter($t('components.grid.back')) }}
              </button>
              <button class="" @click="ConfirmCategDeletion">
                <i class="icon-delete mr-2"></i>
                <h6 class="delete-">
                  {{
      $globals.capitalizeFirstLetter(
        $t('components.grid.deleteCategory'),
      )
    }}
                </h6>
              </button>
            </div>
            <div class="form-group-flex border-b">
              <input-field class="mb-7" name="name" :label="$globals.capitalizeFirstLetter(
        $t('components.grid.categoryName'),
      )
      " :defaultValue="categoryModel && categoryModel?.name" :key="keyInputCateg" :has-error="errors?.categories
      ? true
      : false || errors?.categoryName
        ? true
        : false
      " :error-message="errors && errors?.categories != null
      ? $t('errors.' + errors.categories[0])
      : errors && errors?.categoryName != null
        ? $t('errors.' + errors.categoryName[0])
        : ''
      " @input="addCategoryToGrid" @blur="detectChange" />
            </div>
            <h3 class="mt-6 mb-7">
              <strong>
                {{
      $globals.capitalizeFirstLetter($t('components.grid.items'))
    }}
              </strong>

            </h3>
            <draggable v-if="categoryModel &&
      categoryModel.items &&
      categoryModel.items.length > 0
      " v-model="categoryModel.items" :animation="300">
              <transition-group type="transition" name="flip-list">
                <div class="item-drag" v-for="item in categoryModel.items" :key="item.id" @click="editItem(item)">
                  <i class="icon-drag mr-3"></i>
                  {{ item.name }}
                </div>
              </transition-group>
            </draggable>

            <div class="add-categr mt-4">
              <button @click="showNextItem">
                <span class="mr-2"><i class="icon-plus"></i></span>
                {{
      $globals.capitalizeFirstLetter($t('components.grid.addItem'))
    }}
              </button>
            </div>
            <span class="mt-2 error" v-if="(errors != null && errors?.items != null) ||
      errors?.itemNameErrorMessage != null
      ">
              {{
      (errors && errors?.items != null) ||
        errors?.itemNameErrorMessage != null
        ? $t(
          $globals.capitalizeFirstLetter(
            $t('errors.CATEGORIY_REQUIRED'),
          ),
        )
        : ''
    }}
            </span>
          </div>
          <div class="block" :class="{ active: activeBlock === 2 }">
            <div class="header_bloc">
              <button @click="showPreviousItem">
                <i class="icon-previous mr-2"></i>
                {{ $globals.capitalizeFirstLetter($t('components.grid.back')) }}
              </button>
              <button class="" @click="ConfirmItemDeletion">
                <i class="icon-delete mr-2"></i>
                <h6 class="delete-">
                  {{
      $globals.capitalizeFirstLetter(
        $t('components.grid.deleteItem'),
      )
    }}
                </h6>
              </button>
            </div>
            <div class="form-group-flex critere_lbl border-b">
              <div class="form-field mb-4">
                <label class="control-label">
                  {{
        $globals.capitalizeFirstLetter($t('components.grid.item'))
      }}
                </label>

                <input-field :defaultValue="itemModel.name" :key="keyInputItem" @input="addItemToCategoryModel"
                  :has-error="errors?.items
      ? true
      : false || errors?.nameItem
        ? true
        : false
      " :error-message="errors && errors?.items != null
      ? $t('errors.' + errors.items[0])
      : errors && errors?.nameItem != null
        ? $t('errors.' + errors.nameItem[0])
        : ''
      " @blur="detectChange" />
              </div>
            </div>

            <div class="list-status">

              <ul>
                <li v-for="item in itemModel.notes" :key="item.id" :class="{
      red: item.status === 0,
    }" @click="showNext(item)">
                  {{ item.name }}
                  <span class="pl-5" v-if="item.name !== 'Non applicable' && item.status === 1">
                    {{ displayScore(item.score) }}
                  </span>
                  <span class="pl-5" v-else-if="item.name !== 'Non applicable'">
                    -
                  </span>
                </li>
              </ul>
            </div>
          </div>
          <div class="block" :class="{ active: activeBlock === 3 }">
            <div class="header_bloc">
              <button @click="showPreviousNote(noteModel)">
                <i class="icon-previous mr-2"></i>
                {{ $globals.capitalizeFirstLetter($t('components.grid.back')) }}
              </button>
            </div>
            <div class="border-b pb-7">
              <strong>
                <h6>{{ noteModel.name }}</h6>
              </strong>
            </div>
            <div class="form-status mt-7">
              <div class="form-group-flex mb-4" v-if="noteModel.name != 'Conforme' &&
      noteModel.name != 'Non conforme'
      ">
                <div class="form-field">
                  <label class="control-label">
                    {{
      $globals.capitalizeFirstLetter(
        $t('components.grid.enable'),
      )
    }}
                  </label>
                </div>
                <div class="form-field">
                  <div class="switch-button">
                    <input class="switch-button-checkbox" type="checkbox" :defaultValue="noteModel.status"
                      :checked="getNoteModel.status" @change="() => {
      this.setNoteModel({
        ...this.getNoteModel,
        ['status']: this.getNoteModel.status === 1 ? 0 : 1,
      }),
        this.updateNote(this.noteModel);
    }
      " @blur="detectChange" />
                    <label class="switch-button-label" for="">
                      <span class="switch-button-label-span">Oui</span>
                    </label>
                  </div>
                </div>
              </div>

              <div class="form-group-flex" v-if="noteModel.name !== 'Non applicable' && noteModel.status == 1
      ">
                <div class="form-field">
                  <label class="control-label">
                    {{
      $globals.capitalizeFirstLetter(
        $t('components.grid.point'),
      )
    }}
                  </label>
                </div>
                <div class="form-field label-no">
                  <input-field v-if="noteModel.name !== 'Non applicable'" :defaultValue="formatScore(noteModel.score)"
                    @input="(e) => {
      this.setNoteModel({
        ...this.getNoteModel,
        ['score']: e.target.value,
      }),
        this.updateNote(this.noteModel);
    }
      " :has-error="itemError && !noteModel.score" :errorMessage="itemNameErrorMessage" @blur="detectChange" />
                </div>
              </div>

              <div class="form-group-flex" v-if="noteModel.status == 1">
                <div class="form-field pt-7" style="display: flex; align-items: start">
                  <label class="control-label">
                    {{
      $globals.capitalizeFirstLetter(
        $t('components.grid.repository'),
      )
    }}
                    <i class="icon-info-light ml-2"></i>
                  </label>
                </div>
                <div class="form-field ref mb-4">
                  <textarea rows="3" name="meaning" :value="noteModel.meaning" @input="(e) => {
      this.setNoteModel({
        ...this.getNoteModel,
        ['meaning']: e.target.value,
      }),
        this.updateNote(this.noteModel);
    }
      " @blur="detectChange"></textarea>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div id="overlay-" @click="handleClose()"></div>
      <div class="modal-confirm" v-if="dispalyCommentModal" :key="commentKey">
        <div class="modal-content-">
          <div class="modal-header">
            <h3>Commentaire</h3>
            <button @click="handleClosePopUp()">
              <i class="icon-exit-"></i>
            </button>
          </div>

          <div class="form-field">
            <label>Comment</label>
            <textarea name="" id="" cols="30" rows="10" v-model="commentValue"></textarea>
            <span v-if="commentIsRequired" style="color: red">
              {{ this.commentErr }}
            </span>
            <div class="btn mt-3">
              <Button @when-cliked="handleOnSubmitComment" :text="$globals.capitalizeFirstLetter($t('common.save'))" />
            </div>
          </div>
        </div>
      </div>
      <div class="modal-confirm modal-dialog" v-if="dispalyConfirmationModal" :key="confirmationKey">
        <div class="modal-content-">
          <div class="modal-header">
            <h4 class="text-center w-100">Confirmation</h4>
            <button @click="handleCloseModalConfirmation()">
              <i class="icon-exit-"></i>
            </button>
          </div>

          <div class="form-field--">
            <p class="text-center">
              {{
      $globals.capitalizeFirstLetter(
        $t('components.grid.ConfirmationPub'),
      )
    }}
            </p>
            <div class="modal-footer mt-8">
              <div class="btn">
                <button class="mr-5 btn_cancel" @click="handleCloseModalConfirmation()">
                  {{ $globals.capitalizeFirstLetter($t('common.cancel')) }}
                </button>
              </div>
              <div class="btn">
                <Button class="btn-modal" @when-cliked="publishGrid"
                  :text="$globals.capitalizeFirstLetter($t('common.save'))" />
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="modif-enreg">
        <modal :show-modal="dispalyArchiveModalCategory" :cancelText="cancelData
      ? $t('components.grid.dontLeave')
      : $t('views.agents.modalBtnleft')
      " :okText="cancelData
      ? $t('components.grid.exit')
      : $globals.capitalizeFirstLetter($t('common.delete'))
      " @onConfirm="
      cancelData
        ? leaveCancelModal()
        : categoryModal
          ? submitDeleteCategory()
          : submitDeleteItem()
      " @close="
      cancelData
        ? colseCancelModal()
        : categoryModal
          ? CancelDeleteCategory()
          : CancelDeleteItem()
      ">
          <template v-slot:title>
            {{
      cancelData
        ? $globals.capitalizeAllLetters(
          $t('components.grid.headerCancelModal'),
        )
        : categoryModal
          ? $globals.capitalizeAllLetters(
            $t('components.grid.headerCategModal'),
          )
            : $globals.capitalizeAllLetters(
            $t('components.grid.headerItemModal'),
            )
            }}
          </template>
          <template v-slot:content>
            <p class="pb-5">
              {{
              cancelData
              ? $t('components.grid.confirmCancel')
              : categoryModal
              ? $t('components.grid.confirmDeleteCateg')
              : $t('components.grid.confirmDeleteItem')
              }}
            </p>
            <p>
              {{
              cancelData
              ? $t('components.grid.cancelText')
              : $t('components.grid.archivalText')
              }}
            </p>
          </template>
        </modal>
      </div>
    </div>
  </transition>
</template>

<script>
import InputField from '@/components/FormWidgets/InputField.vue';
import DropDownMenu from '@/components/DropDownMenu/DropDownMenu.vue';
import draggable from 'vuedraggable';
import SelectSingle from '@/components/FormWidgets/SelectSingle/SelectSingle.vue';
import { mapGetters, mapActions } from 'vuex';
import { v4 as uuidv4 } from 'uuid';
import Button from '@/components/FormWidgets/Button.vue';
import Modal from '@/components/Modal/Modal.vue';

export default {
  components: {
    InputField,
    DropDownMenu,
    draggable,
    SelectSingle,
    Button,
    Modal,
  },
  data() {
    return {
      cancelData: false,
      keyInputCateg: 1,
      keyInputItem: 1,
      idCateg: '',
      idItem: '',
      keyTypeItem: 1,
      keyTypeEmotion: 1,
      keyOrderItem: 10,
      activeBlock: 0,
      totalBlocks: 4,
      categoryName: '',
      itemType: [
        {
          id: 1,
          value: 'word_conformity',
          label: this.$t(`components.grid.word_conformity`),
        },
        {
          id: 2,
          value: 'expression_conformity',
          label: this.$t(`components.grid.expression_conformity`),
        },
        {
          id: 3,
          value: 'words_interdits',
          label: this.$t(`components.grid.words_interdits`),
        },
      ],
      itemOrder: [
        {
          id: '1',
          value: 'presentation',
          label: this.$t(`components.grid.presentation`),
        },
        {
          id: '2',
          value: 'end_script',
          label: this.$t(`components.grid.end_script`),
        },
        {
          id: '3',
          value: 'deroulement_evaluation',
          label: this.$t(`components.grid.deroulement_evaluation`),
        },
      ],
      itemNameError: false,
      itemNameErrorMessage: '',
      itemError: false,
      itemOrderErrorMessage: '',
      afterSave: false,
      typeEmotion: [
        {
          id: 1,
          value: 'sad',
          label: this.$t(`components.grid.sad`),
        },
        {
          id: 2,
          value: 'happy',
          label: this.$t(`components.grid.happy`),
        },
        {
          id: 3,
          value: 'anger',
          label: this.$t(`components.grid.anger`),
        },
        {
          id: 4,
          value: 'neutral',
          label: this.$t(`components.grid.neutral`),
        },
      ],

      dispalyCommentModal: false,
      commentValue: '',
      commentKey: 1,
      commentIsRequired: false,
      commentErr: '',
      closedDrawer: false,
      dispalyConfirmationModal: false,
      confirmationKey: 1,
      dispalyArchiveModalCategory: false,
      confirmationCtagegKey: 1,
      categoryModal: false,
      itemModal: false,
      publish: '',
      changesDetected: false,
    };
  },

  props: {
    isOpened: Boolean,
    dashboardPosition: Boolean,
  },

  computed: {
    ...mapGetters('grid', ['getGridModel', 'getErrors', 'getPagination']),
    ...mapGetters('category', ['getCategoryModel']),
    ...mapGetters('item', ['getItemModel']),
    ...mapGetters('note', ['getNoteModel']),
    ...mapGetters('campaign', ['getCampagneInfo']),
    gridModel() {
      return this.getGridModel;
    },
    categoryModel() {
      return this.getCategoryModel;
    },
    itemModel() {
      return this.getItemModel;
    },
    noteModel() {
      return this.getNoteModel;
    },
    errors() {
      return this.getErrors;
    },
    campagnes() {
      return this.$store.getters['campaign/getCollection'].map((element) => ({
        id: element.campagneId,
        name: element.name,
      }));
    },
    campagneSelected() {
      if (this.dashboardPosition) {
        this.$store.commit(
          'grid/SET_CAMPAGNE_ID',
          this.getCampagneInfo.campagneId,
        );
        return this.getCampagneInfo.campagneId;
      } else {
        return this.getGridModel?.campagne?.campagneId;
      }
    },
    gridName() {
      if (this.dashboardPosition) {
        return this.getCampagneInfo.gridSelected != null
          ? this.getCampagneInfo.gridSelected.name
          : '';
      } else {
        return this.getGridModel?.name;
      }
    },
  },

  methods: {
    ...mapActions('grid', [
      'setGridModel',
      'resetGridModel',
      'storeGrid',
      'setErrors',
      'updateGrid',
      'updateCategoryInGrid',
      'deleteCategoryFromGrid',
      'setPagination',
      'saveGridVersion',
      'deleteItemFromGrid',
      'unlockGrid',
    ]),
    ...mapActions('category', [
      'setCategoryModel',
      'addCategory',
      'resetCategoryModel',
      'deleteItemFromCategory',
    ]),
    ...mapActions('item', [
      'setItemModel',
      'addItem',
      'updateNote',
      'resetItemModel',
      'deleteItem',
    ]),
    ...mapActions('note', ['setNoteModel']),
    detectChange() {
      this.changesDetected = true;
    },
    displayScore(score) {
      if (score != null) {
        if (score === '0') {
          return score;
        } else if (score.includes('-')) {
          return score;
        } else if (score.includes('+')) {
          return score;
        } else {
          return `+${score}`;
        }
      }

    },
    formatScore(score) {
      if (score != null) {
        return score.replace('+', '');
      } else {
        return score
      }

    },
    colseCancelModal() {
      this.cancelData = false;
      this.dispalyArchiveModalCategory = false;
    },
    leaveCancelModal() {
      this.cancelData = false;
      this.dispalyArchiveModalCategory = false;
      this.handleClose();
    },
    cancel() {
      if (this.changesDetected) {
        this.cancelData = true;
        this.dispalyArchiveModalCategory = true;
        this.Nocancel();
      } else {
        this.handleClose();
      }
    },
    Nocancel() {
      const modifEnreg = document.querySelector('.modif-enreg');
      modifEnreg.classList.add('non-enregistr');
    },

    handleClose() {
      if (this.gridModel.id != null) {
        this.unlockGrid(this.gridModel.id);
      }
      this.changesDetected = false;
      this.activeBlock = 0;
      this.$emit('close');
      this.setErrors(null);
      this.resetGridModel();
      this.resetCategoryModel();
      this.resetItemModel();
      this.commentErr = '';
      (this.commentValue = ''), (this.dispalyCommentModal = false);
      this.dispalyArchiveModalCategory = false;
      this.dispalyConfirmationModal = false;
      this.closedDrawer = true;
    },
    handleClosePopUp() {
      this.dispalyCommentModal = false;
    },

    showNext(note) {
      if (this.itemModel.name.trim() != '') {
        this.setNoteModel(note);
        this.activeBlock = (this.activeBlock + 1) % this.totalBlocks;
      } else {
        let errorsItem = [];
        errorsItem?.push('ITEM_REQUIRED');
        this.setErrors({ ...this.errors, items: errorsItem });
        this.itemNameErrorMessage = this.$t(
          this.$globals.capitalizeFirstLetter(this.$t('errors.ITEM_REQUIRED')),
        );
      }
    },
    addCategoryToGrid(e) {
      this.idCateg = uuidv4();
      let idCategory =
        this.categoryModel.id != null ? this.categoryModel.id : this.idCateg;
      let result = this.gridModel?.categories?.filter(
        (item) => item.id == idCategory,
      );
      this.setCategoryModel({
        ...this.categoryModel,
        id: idCategory,
        name: e.target.value,
      });
      if (result && result?.length > 0) {
        result[0].id = idCategory;
        result[0].name = e.target.value;
        if (e.target.value.trim() !== '') {
          this.updateCategoryInGrid(result[0]);
        } else {
          if (idCategory.length >= 15) {
            this.deleteCategoryFromGrid(idCategory);
            this.resetCategoryModel();
          }
        }
      } else {
        let newCateg = this.getGridModel.categories;
        if (e.target.value.trim() != '') {
          newCateg.push({
            id: idCategory,
            name: e.target.value,
            items: [],
          });
          this.setGridModel({
            ...this.getGridModel,
            categories: newCateg,
          });
        } else {
          this.setCategoryModel({
            ...this.categoryModel,
            id: null,
            name: '',
          });
        }
      }
    },
    showNextCategory() {
      this.keyInputCateg++;
      if (
        this.gridModel &&
        this.gridModel?.name.trim() !== '' &&
        (this.gridModel?.campagneId != null ||
          this.gridModel?.campagne?.campagneId != null)
      ) {
        this.activeBlock = (this.activeBlock + 1) % this.totalBlocks;
      } else {
        let errors = [];
        if (this.gridModel?.name.trim() === '') {
          errors = 'GRID_NAME_REQUIRED';
          this.setErrors({ ...this.errors, ['name']: errors });
        }
        if (this.gridModel?.campagneId === null) {
          errors = 'CAMPAGNE_REQUIRED';
          this.setErrors({ ...this.errors, ['campagneId']: errors });
        }
      }
    },
    showNextItem() {
      this.keyInputItem++;
      if (
        this.categoryModel &&
        this.categoryModel?.name &&
        this.categoryModel?.name.trim() != ''
      ) {
        this.activeBlock = (this.activeBlock + 1) % this.totalBlocks;
      } else {
        let errorsCateg = [];
        errorsCateg.push('CATEGORIE_REQUIRED');
        this.setErrors({ ...this.errors, categories: errorsCateg });
      }
    },
    showPreviousCategory() {
      if (this.categoryModel.id == null) {
        if (this.categoryModel?.name?.trim() != '') {
          let copy = { ...this.errors };
          delete copy?.categories;
          this.setErrors(copy);
          this.deleteItem();
          this.resetCategoryModel();
        }
        this.activeBlock =
          (this.activeBlock - 1 + this.totalBlocks) % this.totalBlocks;
      } else {
        if (this.categoryModel?.name?.trim() != '') {
          this.resetCategoryModel();

          this.activeBlock =
            (this.activeBlock - 1 + this.totalBlocks) % this.totalBlocks;
        } else {
          let errors = [];
          errors.push('CATEGORIE_REQUIRED');
          this.setErrors({ ...this.errors, ['categoryName']: errors });
        }
      }
    },
    addItemToCategoryModel(e) {
      this.idItem = uuidv4();
      let itemId =
        this.itemModel?.id != null ? this.itemModel?.id : this.idItem;

      let result = this.categoryModel?.items?.filter(
        (item) => item?.id == itemId,
      );
      this.setItemModel({
        id: itemId,
        name: e.target.value,
        notes: this.itemModel.notes,
        emotion: this.itemModel.emotion,
        withIa: this.itemModel.withIa,
      });
      if (result?.length > 0) {
        this.categoryModel.items = this.categoryModel.items.map((item) => {
          if (item?.id === itemId) {
            if (e.target.value.trim() !== '') {
              return {
                ...item,
                id: itemId,
                name: e.target.value,
                notes: this.itemModel.notes,
                emotion: this.itemModel.emotion,
              };
            } else {
              if (item?.id.length >= 15) {
                this.deleteItemFromCategory(item?.id);

                this.resetItemModel();
              }
            }
          }
          return item;
        });
      } else {
        let item = {
          id: itemId,
          name: e.target.value,
          notes: this.itemModel.notes,
          emotion: this.itemModel.emotion,
        };
        if (item.name.trim() != '') {
          this.categoryModel?.items?.push(item);
        } else {
          this.setItemModel({
            ...item,
            id: null,
            name: '',
            notes: this.itemModel.notes,
            emotion: this.itemModel.emotion,
            withIa: this.itemModel.withIa,
          });
        }
      }
      this.setCategoryModel({
        ...this.categoryModel,
      });

      let updatedCategories = this.gridModel.categories.map((category) => {
        if (category?.id === this.categoryModel?.id) {
          return {
            ...category,
            items: this.categoryModel.items,
          };
        }
        return category;
      });

      this.setGridModel({
        ...this.gridModel,
        categories: updatedCategories,
      });
    },
    showPreviousItem() {
      if (this.itemModel?.id == null) {
        this.resetItemModel();
        this.itemError = false;
        this.itemOrderErrorMessage = '';
        this.activeBlock =
          (this.activeBlock - 1 + this.totalBlocks) % this.totalBlocks;
        this.keyOrderItem++;
        this.keyTypeItem++;
        this.keyTypeEmotion++;
      } else {
        if (this.hasEmptyRequiredFields()) {
          this.handleEmptyFields();
        } else {
          this.resetItemModel();
          this.itemError = false;
          this.itemOrderErrorMessage = '';
          this.activeBlock =
            (this.activeBlock - 1 + this.totalBlocks) % this.totalBlocks;
          this.keyOrderItem++;
          this.keyTypeItem++;
          this.keyTypeEmotion++;
        }
      }
    },

    hasEmptyRequiredFields() {
      return this.itemModel?.name?.trim() == '';
    },

    handleEmptyFields() {
      this.itemError = true;
      this.itemNameErrorMessage = this.$t(
        this.$globals.capitalizeFirstLetter(this.$t('errors.FIELD_REQUIRED')),
      );
    },

    showPreviousNote() {
      if (
        this.noteModel?.name !== 'Non applicable' &&
        this.noteModel?.score?.trim() === ''
      ) {
        this.itemError = true;
        this.itemNameErrorMessage = this.$t(
          this.$globals.capitalizeFirstLetter(this.$t('errors.FIELD_REQUIRED')),
        );
      } else {
        let formattedScore = this.noteModel.score;
        if (formattedScore != null) {
          if (
            formattedScore.startsWith('0') &&
            formattedScore.indexOf('.') === -1 &&
            formattedScore.indexOf(',') === -1
          ) {
            formattedScore = formattedScore.replace(/^0+/, '');
          }
        }

        this.noteModel.score = formattedScore;
        this.updateNote(this.noteModel);
        this.activeBlock =
          (this.activeBlock - 1 + this.totalBlocks) % this.totalBlocks;
      }
    },
    submitDeleteItem() {
      this.deleteItemFromCategory(this.itemModel.id);
      this.deleteItemFromGrid(this.itemModel.id);
      this.activeBlock =
        (this.activeBlock - 1 + this.totalBlocks) % this.totalBlocks;
      this.resetItemModel();
      this.CancelDeleteItem();
    },
    submitDeleteCategory() {
      this.deleteCategoryFromGrid(this.categoryModel.id);
      this.activeBlock =
        (this.activeBlock - 1 + this.totalBlocks) % this.totalBlocks;
      this.resetCategoryModel();
      this.CancelDeleteCategory();
    },
    ConfirmItemDeletion() {
      this.dispalyArchiveModalCategory = true;
      this.itemModal = true;
      this.confirmationCtagegKey++;
    },
    ConfirmCategDeletion() {
      this.dispalyArchiveModalCategory = true;
      this.categoryModal = true;
      this.confirmationCtagegKey++;
    },
    CancelDeleteCategory() {
      this.dispalyArchiveModalCategory = false;
      this.categoryModal = false;
    },
    CancelDeleteItem() {
      this.dispalyArchiveModalCategory = false;
      this.itemModal = false;
    },
    handleCloseModalConfirmation() {
      this.dispalyConfirmationModal = false;
    },
    selectedOption(item) {
      if (this.gridModel?.id == null) {
        if (item == 1) {
          if (
            this.gridModel.name.trim() !== '' &&
            this.gridModel.campagneId != null &&
            this.gridModel?.categories.length >= 1 &&
            this.hasItem()
          ) {
            this.setGridModel({ ...this.gridModel, ['draft']: item });
            this.confirmationKey++;
            this.dispalyConfirmationModal = true;
          } else {
            let errors = [];
            if (this.gridModel.name.trim() === '') {
              errors = 'GRID_NAME_REQUIRED';
              this.setErrors({ ...this.errors, ['name']: errors });
            }
            if (this.gridModel.campagneId === null) {
              errors = 'CAMPAGNE_REQUIRED';
              this.setErrors({ ...this.errors, ['campagneId']: errors });
            }
            if (
              this.gridModel &&
              this.gridModel.categories &&
              this.gridModel.categories.length < 1
            ) {
              let errorsCateg = [];
              errorsCateg?.push('CATEGORIE_REQUIRED');
              this.setErrors({ ...this.errors, categories: errorsCateg });
            }
            if (!this.hasItem()) {
              let errorsItem = [];
              errorsItem?.push('ITEM_REQUIRED');
              this.setErrors({ ...this.errors, items: errorsItem });
              this.itemNameErrorMessage = this.$t(
                this.$globals.capitalizeFirstLetter(
                  this.$t('errors.ITEM_REQUIRED'),
                ),
              );
            }
          }
        } else {
          let errors = {};

          if (
            this.gridModel == undefined ||
            this.gridModel?.name.trim() === ''
          ) {
            errors.name = 'GRID_NAME_REQUIRED';
            this.setErrors(errors);
          } else {
            this.setGridModel({ ...this.gridModel, ['draft']: item });

            this.$store.commit('campaign/ADD_GRID_TO_CAMPAGNE', {
              grid: this.getGridModel,
            });
            this.storeGrid().then(() => {
              this.setPagination({
                ...this.getPagination,
                ['current_page']: 1,
              });
              this.handleClose();
            });
          }
        }
      } else {
        if (item == 0) {
          if (this.gridModel.name.trim() != '') {
            this.$store.commit('user/SET_GRID_USER', {
              gridName: this.getGridModel.name,
            });
            this.$store.commit('campaign/SET_CAMPAGNE_INFO', {
              campagneId: this.getGridModel?.campagneId,
              campagneName:
                this.getGridModel && this.gridModel.campagne
                  ? this.getGridModel.campagne.name
                  : '',
              gridSelected: this.getGridModel,
            });
            this.setGridModel({ ...this.gridModel, ['draft']: item });
            this.updateGrid().then(() => { });
            this.handleClose();
          } else {
            errors = 'GRID_NAME_REQUIRED';
            this.setErrors({ ...this.errors, ['name']: errors });
          }
        } else {
          if (
            this.gridModel?.name.trim() !== '' &&
            (this.gridModel?.campagneId != null ||
              (this.gridModel?.campagne &&
                this.gridModel?.campagne?.campagneId !== null)) &&
            this.gridModel?.categories.length >= 1 &&
            this.hasItem()
          ) {
            this.publish = item;
            this.confirmationKey++;
            this.dispalyConfirmationModal = true;
          } else {
            let errors = [];
            if (this.gridModel?.name.trim() === '') {
              errors = 'GRID_NAME_REQUIRED';
              this.setErrors({ ...this.errors, ['name']: errors });
            }
            if (this.gridModel && this.gridModel?.campagneId == null) {
              if (
                this.gridModel &&
                this.gridModel?.campagne?.campagneId == null
              ) {
                errors = 'CAMPAGNE_REQUIRED';
                this.setErrors({ ...this.errors, ['campagneId']: errors });
              }
            }
            if (this.gridModel && this.gridModel?.categories.length < 1) {
              let errorsCateg = [];
              errorsCateg.push('CATEGORIE_REQUIRED');
              this.setErrors({ ...this.errors, categories: errorsCateg });
            }
            if (!this.hasItem()) {
              let itemmErr = [];
              itemmErr.push('ITEM_REQUIRED');
              this.setErrors({ ...this.errors, items: itemmErr });
              this.itemNameErrorMessage = this.$t(
                this.$globals.capitalizeFirstLetter(
                  this.$t('errors.ITEM_REQUIRED'),
                ),
              );
            }
          }
        }
      }
    },
    hasItem() {
      return this.gridModel.categories.every((category) => {
        return category.items.length > 0;
      });
    },
    publishGrid() {
      if (this.getGridModel && this.getGridModel?.id == null) {
        this.$store.commit('campaign/ADD_GRID_TO_CAMPAGNE', {
          grid: this.getGridModel,
        });
        this.storeGrid().then(() => {
          this.setPagination({
            ...this.getPagination,
            ['current_page']: 1,
          });
          this.handleCloseModalConfirmation();
          this.handleClose();
        });
      } else {
        if (this.getGridModel.draft == 1 && this.publish == 1) {
          this.$store.commit('campaign/SET_CAMPAGNE_INFO', {
            campagneId: this.getGridModel?.campagneId,
            campagneName:
              this.getGridModel && this.gridModel.campagne
                ? this.getGridModel.campagne.name
                : '',
            gridSelected: this.getGridModel,
          });
          this.setGridModel({ ...this.gridModel, ['draft']: this.publish });
          this.handleCloseModalConfirmation();
          this.dispalyCommentModal = true;
          this.commentKey++;
        } else {
          this.setGridModel({ ...this.gridModel, ['draft']: this.publish });
          this.$store.commit('user/SET_GRID_USER', {
            gridName: this.getGridModel.name,
          });
          this.$store.commit('user/SET_CAMPAGNE_USER', {
            campagneId: this.getGridModel?.campagneId,
            campagneName:
              this.getGridModel && this.gridModel.campagne
                ? this.getGridModel.campagne.name
                : '',
            gridSelectedId: this.getGridModel.id,
          });
          this.$store.commit('campaign/SET_CAMPAGNE_INFO', {
            campagneId: this.getGridModel?.campagneId,
            campagneName:
              this.getGridModel && this.gridModel.campagne
                ? this.getGridModel.campagne.name
                : '',
            gridSelected: this.getGridModel,
          });
          this.updateGrid().then(() => {
            this.handleCloseModalConfirmation();
            this.handleClose();
          });
        }
      }
    },
    editCategory(item) {
      this.afterSave = false;
      this.activeBlock = (this.activeBlock + 1) % this.totalBlocks;

      this.setCategoryModel({
        ...item,
      });
    },
    editItem(item) {
      this.activeBlock = (this.activeBlock + 1) % this.totalBlocks;
      this.setItemModel({ ...item });
      this.name = this.getItemModel.name;
      this.keyTypeItem++;
      this.keyOrderItem++;
      this.keyTypeEmotion++;
    },
    handleOnSubmitComment() {
      if (this.commentValue.trim() === '') {
        this.commentIsRequired = true;
        this.commentErr = this.$globals.capitalizeFirstLetter(
          this.$t('views.grids.requiredComment'),
        );
      } else {
        let item = {
          gridId: this.gridModel.id,
          comment: this.commentValue,
        };
        this.$store.commit('user/SET_GRID_USER', {
          gridName: this.getGridModel.name,
        });
        this.saveGridVersion(item).then(() => {
          this.commentIsRequired = false;
        });
        this.updateGrid().then(() => { });
        this.handleClose();
      }
    },
  },
  watch: {
    commentValue: function (newValue) {
      if (newValue.trim() === '') {
        this.commentIsRequired = true;
      } else {
        this.commentIsRequired = false;
      }
    },

    'categoryModel.name': function (newValue) {
      let errors = [];
      let copy;
      if (!this.dashboardPosition) {
        if (this.afterSave == false) {
          if (newValue && newValue == '') {
            errors.push('CATEGORIE_REQUIRED');
          }
          if (errors.length > 0) {
            this.setErrors({ ...this.errors, ['categoryName']: errors });
          } else {
            copy = this.errors;
            if (
              copy &&
              copy?.categoryName != null &&
              copy?.categoryName != undefined
            );
            {
              delete copy?.categoryName;
              delete copy?.categories;
              this.setErrors(copy);
            }
          }
        }
      } else {
        if (newValue && newValue == '') {
          errors.push('CATEGORIE_REQUIRED');
        }
        if (errors && errors?.length > 0) {
          this.setErrors({ ...this.errors, ['categoryName']: errors });
        } else {
          copy = this.errors;
          if (copy && copy != null && copy != undefined);
          {
            if (copy != null) {
              delete copy.categoryName;
              delete copy.categories;
              this.setErrors(copy);
            }
          }
        }
      }
    },
    'gridModel.name': function (newValue) {
      let errors = [];
      if (this.closedDrawer == false) {
        if (newValue && newValue.trim() == '') {
          errors.push('GRID_NAME_REQUIRED');
        }
        if (errors && errors?.length > 0) {
          this.setErrors({ ...this.errors, ['name']: errors });
        } else {
          let copy = this.errors;
          if (copy && copy.name != null) {
            delete copy.name;
            this.setErrors(copy);
          }
        }
      }
    },
    gridModel: {
      handler(newValue) {
        if (
          newValue &&
          typeof newValue === 'object' &&
          newValue?.campagneId !== undefined &&
          newValue?.campagneId !== null
        ) {
          let errors = [];
          if (newValue?.campagneId === '') {
            errors.push('CAMPAGNE_REQUIRED');
          }
          if (errors.length > 0) {
            this.setErrors({ ...this.errors, ['campagneId']: errors });
          } else {
            let copy = this.errors;
            if (copy && copy != null && copy.campagneId) {
              delete copy.campagneId;
              this.setErrors(copy);
            }
          }
        } else {
          if (
            newValue &&
            typeof newValue === 'object' &&
            newValue?.name !== undefined &&
            newValue?.name !== null
          ) {
            let copy = this.errors;
            if (copy && copy != null && copy.name) {
              delete copy.name;
              this.setErrors(copy);
            }
          }
        }
      },
    },
    'itemModel.name': function (newValue) {
      let errors = [];
      if (this.closedDrawer == false) {
        if (newValue && newValue.trim() === '') {
          errors.push('ITEM_REQUIRED');
        }
        if (errors && errors?.length > 0) {
          this.setErrors({ ...this.errors, ['ITEM_REQUIRED']: errors });
          this.itemNameErrorMessage = this.$t(
            this.$globals.capitalizeFirstLetter(
              this.$t('errors.ITEM_REQUIRED'),
            ),
          );
        } else {
          let copy = this.errors;
          if (copy && copy.name != null) {
            delete copy.name;
            this.setErrors(copy);
          }
        }
      }
    },
    'categoryModel.items': function (newValue) {
      let errors = [];
      if (this.closedDrawer == false) {
        if (this.activeBlock == 0) {
          if (
            this.gridModel &&
            this.gridModel.id != null &&
            this.gridModel.categories.length > 0 &&
            this.gridModel.categories.some(
              (category) => category.items && category.items.length > 0,
            ).length < 1
          ) {
            errors.push('ITEM_REQUIRED');
          }
          if (errors && errors?.length > 0) {
            this.setErrors({ ...this.errors, ['nameItem']: errors });
            this.itemNameErrorMessage = this.$t(
              this.$globals.capitalizeFirstLetter(
                this.$t('errors.ITEM_REQUIRED'),
              ),
            );
          } else {
            let copy = this.errors;
            if (copy?.nameItem != null) {
              delete copy.nameItem;
              this.setErrors(copy);
              this.itemNameErrorMessage = '';
            }
          }
        } else {
          if (
            this.activeBlock == 2 &&
            this.categoryModel &&
            this.categoryModel.id != null &&
            this.categoryModel.items.length < 1
          ) {
            errors.push('ITEM_REQUIRED');
          }
          if (errors && errors?.length > 0) {
            this.setErrors({ ...this.errors, ['nameItem']: errors });
            this.itemNameErrorMessage = '';
          } else {
            let copy = this.errors;
            if (copy && copy.items != null) {
              delete copy.items;
              this.setErrors(copy);
              this.itemNameErrorMessage = '';
            }
          }
        }
      }
    },
  },
};
</script>