<template>
  <div class="list_callcenter scroll"> 
  
    <progress-bar v-for="(element, index) of data " :key="index" :target="element.avgScore" :color="getProgressBarColor(index)" :title="element.callCenterName"></progress-bar>
  </div>
</template>

<script>
import ProgressBar from '@/components/Dashboard/Progressbar/Progressbar.vue';
export default {
  props: ['data'],
  components: {
    ProgressBar,
  },
  data() {
    return {
      progressBarColors: ['#4caf50', '#fa850f', '#FCEF21', 'red'],
      renderKey:0,

    };
  },

  methods: {
     getProgressBarColor(index) {
    return this.progressBarColors[index % this.progressBarColors.length];
  },
  },

  computed: {},
  mounted() {
    this.renderKey++
  },
};
</script>
